.ManageOneTapLoginTokens .token-container
{
  border: 1px solid #e3e5e6;
  border-radius: 12px;
  background-color: #f7f9fa;
  margin: 64px auto 16px;
  width: 342px;
}

.ManageOneTapLoginTokens .token-container .token
{
  padding: 16px;
}

.ManageOneTapLoginTokens .token-container .token:not(:first-child)
{
  border-top: 1px solid #e3e5e6;
}

.ManageOneTapLoginTokens .token-container .token .token-icon
{
  border-radius: 8px;
  box-shadow: 0 .5px 2px rgba(0,0,0,.08);
  width: 40px;
  display: inline-block;
  margin-right: 16px;
  vertical-align: top;
}

.ManageOneTapLoginTokens .token-container .token .token-info-container
{
  margin-right: 16px;
  display: inline-block;
  vertical-align: middle;
  width: 212px;
}

.ManageOneTapLoginTokens .token-container .token .token-info-container .token-name
{
  font-size: 16px;
  color: #0eadff;
  line-height: 1em;
  font-weight: 500;
  margin-bottom: 4px;
}

.ManageOneTapLoginTokens .token-container .token .token-info-container .token-time
{
  font-size: 11px;
  color: #73828b;
  line-height: 1.5em;
  list-style-type: disc;
  list-style-position: outside;
}

.ManageOneTapLoginTokens .token-container .token .revoke-icon
{
  background: url(/accounts/static/images/revoke.png);
  width: 24px;
  height: 24px;
  border: 0;
  display: inline-block;
  outline: 0;
  vertical-align: middle;
}

.ManageOneTapLoginTokens .token-container .token .revoke-icon:hover
{
  background: url(/accounts/static/images/revoke-hover.png);
}

.ManageOneTapLoginTokens .token-container .token .revoke-icon:active,.ManageOneTapLoginTokens .token-container .token .revoke-icon:focus
{
  background: url(/accounts/static/images/revoke-active.png);
}

@media (max-width:350px)
{
  .ManageOneTapLoginTokens .token-container
  {
    width: 100%;
  }

  .ManageOneTapLoginTokens .token-container .token .token-icon
  {
    display: none;
  }

  .ManageOneTapLoginTokens .token-container .token .token-info-container,.ManageOneTapLoginTokens .token-container .token .token-sessions .token-session .session-since
  {
    width: calc(100% - 40px);
